import { createSlice } from '@reduxjs/toolkit'
export const shopSlice  = createSlice({
   name: 'shop',
   initialState: {
      siteid: null,
      settings: [],
      activeSetting: {},
      user: {},
      dash: {},
      receipt: { show: false, paytype: 'normal', header: {}, body: [] },
      requist: {},
      products: [],
      cart: [],
      session: [],
      recent: [],
      currentPage: 0
   },
   reducers: {
      setProducts: (state, { payload }) => {
        state.products = payload
      },

      setUserData: (state, { payload }) => {
        state.user = payload
      },

      setDash: (state, { payload }) => {
        state.dash = payload
      },

      updateDash: (state, { payload }) => {
        state.dash = {...state.dash, ...payload }
      },
     
      setReceipt: (state, { payload }) => {
        state.dash = payload
      },

      updateReceipt: (state, { payload }) => {
        state.dash = {...state.dash, ...payload }
      },

      setCurrentPage: (state, { payload }) => {
        state.currentPage = payload
      },

      setSiteId: (state, { payload }) => {
        state.siteid = payload
      },

      setSettings: (state, { payload }) => {
        state.settings = payload
      },

      setActiveSetting: (state, { payload }) => {
        state.activeSetting = payload
      },
     
    
   }
})

export const { setProducts,setUserData,setDash,updateDash,setReceipt,updateReceipt,setCurrentPage,setSiteId,setSettings,setActiveSetting } = shopSlice.actions;

export default shopSlice.reducer;