import React from 'react'

const Pager = ({ count,page,keyword,onPageChange,onPageClick,onSearchChange,onSubmitSearch,isLoading }) => {
    return (
        <div class="form-group row mb-2">
            <div class="col-md-8">
                <fieldset class="form-group mb-0 d-flex barcodeselection">
                    <input type="text" className="form-control border-dark" value={keyword} onChange={onSearchChange} onKeyUp={onSearchChange} placeholder="SEARCH ..."/>
                </fieldset>
            </div>
            <div class="col-md-4">
                <div className="btn-group">
                    <button className="btn btn-sm alert-dark py-2 px-3" onClick={onSubmitSearch}><b><small>SEARCH</small></b></button> &nbsp;
                    <button className="btn btn-sm btn-dark py-2 px-3" disabled={ page <= 1 || isLoading } onClick={(e) => onPageClick( e,parseInt(Math.max(0,page-1)) )} style={{minWidth:'auto'}}><b>PREV</b></button> &nbsp;
                    <button className="btn btn-sm alert-info py-2 px-3"><b>PAGE {page} / {count} </b></button> &nbsp;
                    <button className="btn btn-sm btn-dark py-2 px-3" disabled={ page >= count || isLoading  } onClick={(e) => onPageClick( e,parseInt(Math.min(count,page+1)) )}><b>NEXT</b></button>
                </div>
            </div>
        </div>
    )
}

export default Pager
