import { Link } from 'react-router-dom';
import React,{ useState, useEffect} from 'react';
import Loader from '../assets/images/loadergif.gif';
import Logo from '../assets/images/misc/logo.png';
import Paid from '../assets/images/paid1.jpeg';
import '../pages/Invoice.css';
import '../pages/Print.css';
import InvoiceCSS from '../components/InvoiceCss';
import PageCSS from '../components/PageCss';
import moment from 'moment';
import { withCookies, Cookies } from "react-cookie";
import { useSelector,useDispatch } from 'react-redux';
import Pager from '../components/Pager';
//import {axios} from 'axios';

const DailySalePage = (props) => {

    const user = props.cookies.get('user');
    const [ orders, setOrders ] = useState([]);
    const [ invoice, setInvoice ] = useState({});
    const [ showLoader, setShowLoader ] = useState(true);
    const [ showCover, setShowCover ] = useState(false);
    const { REACT_APP_API_URL } = process.env;
    
    const [ page, setPage ] = useState(1);
    const [ count, setCount ] = useState(1);
    const [ keyword, setKeyword ] = useState('');
    const [ isLoading, setIsLoading ] = useState(false);
    
    const { shop } = useSelector(state => state)
    const dispatch = useDispatch();
    

    const getOrders = async () => {
        setIsLoading(true)
        const condition = user && user.app_access ? ``:`&sess=vat`
        const siteid = shop.siteid
        var query = `?siteid=${siteid}${condition}`;
        if(page >= 1) query += `&page=${page-1}`
        if(keyword != '') query += `&keyword=${keyword}`
        
        const res = await fetchOrders(query);
        console.log(res)
        if(res.success) {
            setIsLoading(false)
            setCount(res.data.totalPages)// Total Pages
            setOrders([...res.data.data]);// Page Data
        }
    }


    const onSearchChange = async (e) => {
        setPage(1)
        setKeyword(e.target.value)
        if(e.target.value == '') getOrders()
        if(e.target.value)
          setTimeout(()=>{
             setKeyword(e.target.value)
             getOrders() 
          },200)
     }
   
     const onPageChange = async (e) => {
       var vs = parseInt(e.target.value)
       vs = Math.max(1,vs)
       vs = Math.min(count,vs)
       setPage(vs)
       setIsLoading(true)
     }
   
     const onPageClick = async (event,value) => {
       setIsLoading(true)
       setPage(value);
     }
      
     const onSubmitSearch = async (e) => {
       e.preventDefault()
       setIsLoading(true)
       getOrders()
     }


    // Initialize
    useEffect(()=>{
        getOrders();
    },[]);

    
    // Fetch Orders
    const fetchOrders = async (query) => {
       const res = await fetch(`${REACT_APP_API_URL}/dailysales${query}`,{ mode:'cors' })
       const data = await res.json();
       return data;
    }


    // Edit User
    const viewInvoice = async (id)=>{
       const order = orders.filter( order => order._id === id);
       setInvoice(order[0]);
       setShowCover(true);
    }

    const printSales = () => {
        var content = document.getElementById("print");
        var pri = document.getElementById("printer");
        const style = document.createElement("style");
            style.innerHTML = PageCSS;
        const cover = document.createElement("div");
            cover.setAttribute('id','print')
            cover.innerHTML = content.innerHTML
        pri.contentWindow.document.open();
        pri.contentWindow.document.write(cover.outerHTML);
        pri.contentDocument.head.appendChild(style)
        pri.contentWindow.document.close();
        pri.contentWindow.focus();
        pri.contentWindow.print();
    }


    const printReceipt = () => {
        var content = document.querySelector(".invoice1");
        var pri = document.getElementById("printer");
        const style = document.createElement("style");
            style.innerHTML = InvoiceCSS;
        const cover = document.createElement("div");
            cover.setAttribute('id','Invoice-pos')
            cover.innerHTML = content.innerHTML
        pri.contentWindow.document.open();
        pri.contentWindow.document.write(cover.outerHTML+cover.outerHTML);
        pri.contentDocument.head.appendChild(style)
        pri.contentWindow.document.close();
        pri.contentWindow.focus();
        pri.contentWindow.print();
    }
 
   
    /* PAGES */

   
    const ListPage = () => (
        <div id="print">
            <h1 className="no-screen">DAILY SALES</h1>{/*  -- { moment().format('MMM DD, YYYY').toUpperCase()}<br/> [ <b className="text-success">GHC {filteredOrders.reduce((acc,rd) => acc+rd.amount,0).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</b> ] */}
            <div className="table-responsive" id="printableTable">
            <table id="productUnitTable" className="display dataTable no-footer" role="grid" aria-describedby="productUnitTable_info">
                <thead className="text-body">
                    <tr role="row">
                        <th>SALES DATE</th>
                        <th>SALES TYPE</th>
                        <th>RECEIPT NO</th>
                        <th>AMOUNT (GHC)</th>
                        <th>SALES AGENT</th>
                        <th className="no-print">ACTION</th>
                    </tr>
                </thead>
                <tbody className="kt-table-tbody text-dark">
                    {orders.map((order) => (
                        <tr className="kt-table-row kt-table-row-level-0 odd" role="row" key={order._id}>
                            <td><b>{order.created_at.toUpperCase()}</b></td>
                            <td><b><em>{order.ordertype == 'normal' ? 'CASH SALES':'CREDIT SALES'}</em></b></td>
                            <td><em>{order.oid}</em><br/>{order.refname ? <em>[ {order.refname} ]</em>:''}</td>
                            <td><b>GHC {order.amount.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</b></td>
                            <td><small><b>{order.user.toUpperCase()}</b></small></td>
                            <td className="no-print"><button className="btn btn-xs btn-dark"  onClick={() => viewInvoice(order._id)} data-toggle="tooltip" title="Edit" data-placement="right"><small><i className="fas fa-list-ul"></i>&nbsp;&nbsp; <b>VIEW RECEIPT</b></small></button></td>
                        </tr>
                    ))}
                    
                    </tbody>
                </table>
            </div>
        </div>
    );


    const InvoicePage = () => (
        <div id="print1">
            <div id="invoice-POS" className="invoice1">
            <div className="offcanvas-header d-flex align-items-center justify-content-between pb-3">
                <h4 className="font-size-h4 font-weight-bold m-0">RECEIPT: # {invoice.oid}<hr/></h4>
                <button className="btn btn-sm btn-icon btn-light btn-hover-primary p-2" onClick={() => setShowCover(false)}><i className="fa fa-times"></i></button>
            </div>
                
                    <div className="info">
                        <h3>SALES RECEIPT</h3>
                    </div>
               
                <div id="mid">
                    <div>
                        <br/>
                        <p> 
                            <img src={Logo} style={{margin: '-20px 10px 0px 2px ', float: 'left'}} height="100px"/>
                            <b>
                                ADDRESS : Abura Market<br />
                                PHONE   :    &nbsp;&nbsp;054-043-8290 <br/>
                                MOBILE  :    &nbsp;&nbsp;026-928-5774 <br/>
                                INVOICE :  # {invoice.oid} <br/>
                                { invoice.created_at ? `DATE : ${invoice.created_at}`  : '' }
                                <br/>{ invoice.refname ? `CLIENT : ${invoice.refname.toUpperCase()}`  : '' }
                            </b>
                        </p><br/><br/>
                    </div>
                </div>
                <div id="">
                   <div id="table">
                    <table>
                    <tbody>
                    <tr className="tabletitle">
                        <td>Item</td>
                        <td>Unit-Price</td>
                        <td>Qty</td>
                        <td>Sub Total</td>
                    </tr>
                  { invoice.cart ?
                    invoice.cart.map((inv,i) => (
                        <tr className="service" key={i}>
                            <td className="tableitem"><span className="itemtext">{inv.product && inv.product.title ? inv.product.title.toUpperCase() : 'NONE'}</span></td>
                            <td className="tableitem"><span className="itemtext">&#8373; {inv.price.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</span></td>
                            <td className="tableitem"><span className="itemtext">{inv.quantity}</span></td>
                            <td className="tableitem"><span className="itemtext">&#8373; {inv.amount.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</span></td>
                        </tr>
                      ))
                    : 
                     (
                        <tr className="service">
                            <td className="tableitem" COLSPAN="4"><br/><center className="itemtext"><h6 class="text-dark">NO ITEMS IN ORDER ...</h6></center><br/></td>
                        </tr>   
                     )
                    
                  }
                   {/* <tr className="tabletitle shead">
                        <td />
                        <td className="Rate"><h2>tax</h2></td>
                        <td className="payment"><h2>&#8373; { invoice ? invoice.tax : 0 }</h2></td>
                    </tr>
                    <tr className="tabletitle shead">
                        <td />
                        <td className="Rate"><h2>discount</h2></td>
                        <td className="payment"><h2>&#8373; { invoice ? invoice.discount : 0 }</h2></td>
                    </tr>
                */}
                    <tr className="tabletitle shead">
                      <td colSpan="1"/>
                      <td className="Rate" align="right" colSpan="3">
                        <h2>Total :  &#8373;{ invoice.amount ? invoice.amount.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",") : 0 }<br/><center><small><em><b>( VAT Inclusive )</b></em></small></center></h2>
                      </td>
                    </tr>
                    </tbody></table>
                </div>
                </div>
                <div>
                <center style={{margin: '5px auto 0px'}}><b>GOODS SOLD ARE NOT RETURNABLE</b></center><hr/>
			    <center style={{margin: '0px auto'}}><b>THANKS FOR SHOPPING WITH US !!</b></center>
			    {Paid && <img src={Paid} style={{margin: '10px auto', display: 'block', height: '40px'}}/>}
                </div>
            </div>
            <center>
                <button class="btn btn-inline btn-dark btn-xs mx-5 center" onClick={() => printReceipt()}><i className="fas fa-print"></i>&nbsp;&nbsp;<b>PRINT RECEIPT</b></button>
            </center>
            <iframe id="printer" style={{display:'none'}}></iframe>
	    </div>
    );


    return (
        <div className="d-flex flex-column-fluid pt-5">
            <div className="container-fluid pt-5">
                <div className="row">
                    <div className="col-12">
                        <div className="row">
                            <div className="col-lg-12 col-xl-12">
                                <div className="card card-custom gutter-b bg-transparent shadow-none border-0">
                                <div className="card-header align-items-center  border-bottom-dark px-0">
                                 
                                 {/* Page Title */}
                                <div className="card-title mb-0">
                                    <h3 className="card-label mb-0 font-weight-bold text-body">DAILY SALES -- { moment().format('MMM DD, YYYY').toUpperCase()} --- [ DAILY AMOUNT SOLD : <b className="text-success">GHC {orders.reduce((acc,rd) => acc+rd.amount,0).toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</b> ]</h3>
                                </div>

                                 {/* Actions */}
                                 <div className="icons d-flex">
                                    <Link to="#"  className="ml-2" onClick={()=> printSales()}>
                                        <span className="icon h-30px font-size-h5 w-30px d-flex align-items-center justify-content-center rounded-circle ">
                                            <svg width="15px" height="15px" viewBox="0 0 16 16" className="bi bi-printer-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                            <path d="M5 1a2 2 0 0 0-2 2v1h10V3a2 2 0 0 0-2-2H5z" />
                                            <path fillRule="evenodd" d="M11 9H5a1 1 0 0 0-1 1v3a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1v-3a1 1 0 0 0-1-1z" />
                                            <path fillRule="evenodd" d="M0 7a2 2 0 0 1 2-2h12a2 2 0 0 1 2 2v3a2 2 0 0 1-2 2h-1v-2a2 2 0 0 0-2-2H5a2 2 0 0 0-2 2v2H2a2 2 0 0 1-2-2V7zm2.5 1a.5.5 0 1 0 0-1 .5.5 0 0 0 0 1z" />
                                            </svg>
                                        </span>
                                    </Link>
                                </div>
                               
                                
                                </div>
                            </div>
                            </div>
                        </div>

                        {/* List Page */}
                        <div className="row">
                            <div className="col-12 ">
                                <div className="card card-custom gutter-b bg-white border-0">
                                    <div className="card-body">
                                        {<Pager count={count} page={page} onPageChange={onPageChange} onPageClick={onPageClick} keyword={keyword} onSearchChange={onSearchChange} onSubmitSearch={onSubmitSearch} isLoading={isLoading} /> }
                                        {<ListPage/> }
                                    </div>
                                </div>
                            </div>
                        </div>

                         {/* Form Page */}
                        <div id="kt_notes_panel" className={ showCover ? 'offcanvas offcanvas-right kt-color-panel offcanvas-on':'offcanvas offcanvas-right kt-color-panel'}>
                            {InvoicePage()}
                        </div>
                       
                    </div>
                </div>
            </div>
        </div>
       
    )
}




export default withCookies(DailySalePage)

